@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("./reset.scss");
@import url("./stories.scss");

.hero-cont {
  position: relative;
  .gradient-line {
    background: linear-gradient(
      90deg,
      rgba(255, 197, 147, 1) 0%,
      rgba(188, 113, 152, 1) 35%,
      rgba(90, 119, 255, 1) 100%
    );
    height: 6px;
    width: 128px;
    position: absolute;
    top: 0;
  }

  .hero-text-cont {
    position: relative;
    width: 100%;
    padding: 72px 24px 72px 33px;
    background-color: black;
    color: white;

    .hero-text-title {
      font-family: DM Sans;
      font-style: normal;
      font-weight: bold;
      font-size: 32px;
      line-height: 40px;
      /* or 125% */

      letter-spacing: 3.33333px;
      text-transform: uppercase;

      color: #ffffff;
      margin-bottom: 16px;
    }
    .hero-text-body {
      font-family: DM Sans;
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 25px;
      /* or 167% */

      color: #ffffff;

      mix-blend-mode: normal;
      opacity: 0.6;

      margin-bottom: 16px;
    }

    .hero-text-invite {
      font-family: DM Sans;
      font-style: normal;
      font-weight: bold;
      font-size: 12px;
      line-height: 16px;
      /* identical to box height */

      letter-spacing: 2px;

      color: #ffffff;
    }
  }
}

.hero-cont-two {
  .hero-text-cont {
    position: relative;
    width: 100%;
    padding: 72px 24px 72px 33px;
    background-color: white;
    .hero-text-title {
      font-family: DM Sans;
      font-style: normal;
      font-weight: bold;
      font-size: 32px;
      line-height: 40px;
      /* or 125% */

      letter-spacing: 3.33333px;
      text-transform: uppercase;

      color: black;
      margin-bottom: 16px;
    }
    .hero-text-body {
      font-family: DM Sans;
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 25px;
      /* or 167% */
      color: black;
      mix-blend-mode: normal;
      opacity: 0.6;
      margin-bottom: 16px;
    }

    .hero-text-invite {
      font-family: DM Sans;
      font-style: normal;
      font-weight: bold;
      font-size: 12px;
      line-height: 16px;
      /* identical to box height */
      letter-spacing: 2px;
      color: black;
    }
  }
}

.hero-stories-cont {
  .story {
    position: relative;
    .img-overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      background: linear-gradient(
        180deg,
        rgba(0, 0, 0, 0.0001) 0.27%,
        rgba(0, 0, 0, 0.661222) 100%
      );
    }

    .story-text {
      position: absolute;
      z-index: 10;
      bottom: 40px;
      width: 100%;
      padding: 0 33px;

      .story-title {
        font-family: DM Sans;
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 25px;
        /* identical to box height, or 139% */
        color: #ffffff;
        margin-bottom: 4px;
      }

      .story-author {
        font-family: DM Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 13px;
        line-height: 17px;
        /* identical to box height */
        color: #ffffff;
      }

      .read-story {
        font-family: DM Sans;
        font-style: normal;
        font-weight: bold;
        font-size: 12px;
        line-height: 16px;
        /* identical to box height */
        letter-spacing: 2px;
        color: #ffffff;
        border-top: 1px solid rgba(255, 255, 255, 0.25);
        margin-top: 16px;
        padding-top: 20px;
      }
    }
  }
}

.hero-features {
  padding: 80px 32px 80px 33px;
  .feature-cont {
    @apply flex flex-col justify-center items-center;
    .feature-title {
      font-family: DM Sans;
      font-style: normal;
      font-weight: bold;
      font-size: 18px;
      line-height: 25px;
      /* identical to box height, or 139% */

      text-align: center;

      color: #000000;
      margin-top: 48.15px;
    }
    .feature-body {
      font-family: DM Sans;
      font-style: normal;
      font-weight: normal;
      font-size: 15px;
      line-height: 25px;
      /* or 167% */
      text-align: center;
      color: #000000;
      mix-blend-mode: normal;
      opacity: 0.6;
      margin-top: 16px;
    }
  }
}

.footer-cont {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: black;
  color: white;
  padding: 56px 33px;

  .footer-socials {
    margin-top: 32px;
  }

  .footer-links {
    margin-top: 30px;

    li {
      margin-top: 19px;
    }
  }

  .footer-invite {
    display: flex;
    align-items: center;
    margin-top: 119px;
    font-family: DM Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 16px;
    /* identical to box height */

    letter-spacing: 2px;

    color: #ffffff;
  }

  .footer-copyright {
    margin-top: 34px;
    font-family: DM Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 20px;
    /* identical to box height */

    text-align: center;

    color: #ffffff;

    mix-blend-mode: normal;
    opacity: 0.5;
  }
}
